.stylePage {
  &__boxLink {
    width: 100%;
    height: 60px;
    margin: 10vh 0vw;
    position: sticky;
    overflow: hidden;
    overflow-x: scroll;
    top: 10px;
    background-color: #FFF5E2;
    z-index: 2;
    

    & > ul {
      display: flex;
      flex-direction: row;
      justify-content: center;
      //flex-wrap: wrap;
      column-gap: 1.2vw;
      row-gap: 2vh;
      min-height: 100%;
      min-width: 100%;
      width: 220vw;
      position: sticky;
      top: 160px;
      transform: translateX(10vw);
      //border: 2px solid red;
    }
  }
  &__elLink {
    cursor: pointer;
  }

  &__link {
    padding: 14px 21px;
    cursor: pointer;
    font-size: 21px;
    transition: 0.3s;

    &:hover {
      background-color: #f9b949;
      color: #000;

      padding: 6px 21px;
      transition: 0.3s;
    }

    &--actif {
      transition: 0.3s;
      color: #f9b949;
    }
  }

  &__sect {
    width: 100%;
    height: auto;
    padding: 6vh 5vw;
    transition: 0.3s;
    border-bottom: none;
    opacity: 1;

    &--ecart {
      margin: 0vh 0;
    }

    &--small {
      height: 180px;
      overflow: hidden;
      padding: 6vh 5vw;
      transition: 0.6s;
      border-bottom: 1px solid #f9b949;
      &>ul{
        //opacity: 0;
        display: none;
      }
     
    }
  }

  &__listBox {
    width: 100%;
    height: auto;
    padding: 6vh 0vw;
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    row-gap: 5vh;
    column-gap: 3vw;

    &--black{
      color: #FFF5E2;
    }
  }

  &__elBox {
    width: 300px;
    width: calc((100% / 5) - 3vw);
    min-width: 250px;
    max-width: 400px;
    height: 450px;
    display: flex;
    flex-direction: column;
    row-gap: 0.2vh;
    cursor: pointer;
    overflow: hidden;
    transition: 0.3s;

    &--video{
      
      max-width: 450px;
      width: calc(25% - 3vw);
      height: 300px;
      &>img{
        aspect-ratio: 3/1;
      }
    }

    & > h3 {
      word-wrap: normal;
      white-space: pre-wrap;
    }
    &:hover {
      color: #f9b949;

      & > img {
        transition: 0.3s;
        opacity: 0.8;
      }
    }
  }
  &__boxTitle {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    
    
  }
  &__btnToggle{
    cursor: pointer;
    min-width: 120px;
    width: auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    &>span{
      pointer-events: none;
      margin: 0 0 0 6px;
    }
    padding: 12px 21px;

    &:hover {
      transition: 0.3s;
      color: #f9b949;
      fill: #f9b949;
      &>svg{
        fill: #f9b949;
        transition: 0.3s;
        transform: rotate(35deg);
      }
    }
    
  }

  &__svgArrow{
    pointer-events: none;
    transition: 0.15s;
    &:hover{
      transition: 0.3s;
      fill: #f9b949;
    }
    margin: 4px 0 0 0;
    transform: rotate(180deg);
    &--click{
      transition: 0.4s;
      transform: rotate(0deg);

      &:hover{
        transition: 0.3s;
        transform: rotate(135deg);
      }
    }

    &--black{
      fill: #FFF5E2;
    }
  }
  &__imgBox {
    width: 100%;
    height: 380px;
    object-fit: cover;
    padding: 0 0 18px;
    border-radius: 8px;
  }
}

@media(min-width: 600px){
  .stylePage{
    &__boxLink{
      top: 60px;
      margin: 10vh 3vw;
      & > ul {
        row-gap: 0vh;
        width:100%;
        transform: translateX(0vw);
        //border: 2px solid red;
      }
    }

    &__listBox{
      row-gap: 8vh;
    }

    &__sect{
      &--ecart {
        margin: 20vh 0;
      }
    }
  }
}